* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-size: 62.5%;
}

body {
  color: #555;
  font-family: Roboto, sans-serif;
  font-weight: 400;
  line-height: 1;
}

.reusable-header {
  color: #212529;
  letter-spacing: .5px;
  text-align: center;
  margin-bottom: 6.4rem;
  font-family: Caprasimo;
  font-size: 3.6rem;
  font-weight: 500;
}

.nav-section {
  background-color: #f8f9fa;
  padding: 4.8rem 9.6rem;
}

.nav-list {
  justify-content: space-between;
  align-items: center;
  list-style: none;
  display: flex;
}

.nav-list-link:link, .nav-list-link:visited {
  color: #212529;
  letter-spacing: .5px;
  font-family: Caprasimo, cursive;
  font-size: 3rem;
  font-weight: 500;
  text-decoration: none;
  transition: all .3s;
}

.nav-list-link:hover, .nav-list-link:active {
  color: #868e96;
}

.hero-section {
  position: relative;
}

.hero-section-img {
  z-index: 1;
  width: 100%;
  height: auto;
  display: block;
}

.hero-section-img-2 {
  z-index: 2;
  opacity: .5;
  filter: grayscale(.9);
  border-radius: 9px;
  width: 100%;
  height: 100%;
  display: block;
  position: absolute;
  top: 0%;
  right: 0%;
}

.history-section {
  text-align: center;
  background-color: #e9e9ea;
  margin-bottom: 3.6rem;
  padding: 6.4rem;
}

.history-text {
  color: #030404;
  font-size: 2rem;
  font-weight: 400;
  line-height: 1.6;
}

.discography-section {
  margin: 0 auto;
  padding: 6.4rem;
}

.container {
  grid-template-columns: repeat(3, 1fr);
  justify-content: center;
  align-items: center;
  gap: 4.8rem;
  margin-bottom: 4.8rem;
  display: grid;
}

.gallery-div {
  background-color: #e9ecef;
  border-radius: 9px;
  padding: 3.2rem;
  transition: all .5s;
}

.gallery-div:hover {
  transform: scale(1.1);
}

.gallery-div > h3 {
  text-transform: uppercase;
  color: #111;
  letter-spacing: 1.5px;
  height: 5rem;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 1.8rem;
  font-weight: 800;
}

span {
  margin-left: .4rem;
}

.discography-img {
  opacity: .9;
  z-index: 1;
  border-radius: 9px;
  width: 100%;
  height: 30rem;
  display: block;
}

.footer {
  text-align: center;
  background-color: #e9e9ea;
  padding: 6.4rem 9.6rem;
}

.external-links-footer {
  justify-content: center;
  align-items: center;
  gap: 4.6rem;
  display: flex;
}

.wiki-logo, .youtube-logo {
  width: 6rem;
  height: auto;
  display: block;
}

.footer-end {
  text-align: center;
  margin-top: 4rem;
  font-family: Times New Roman, Times, serif;
  font-size: 1.4rem;
  font-weight: 500;
}

.sticky {
  opacity: .9;
  z-index: 3;
  background-color: #fff;
  width: 100%;
  position: fixed;
}

